/** @jsx html */
'use strict';

import {html}   from 'snabbdom-jsx';
import texts    from '../texts';

export default {
    addTrack(evt) {
        evt.preventDefault();
        console.log('TODO: Add Track');
    },
    feedNavbar(props) {
        let state = window.store.getState();

        return <nav
            id="navbar"
            classNames="feed"
            class-hide={state.feed.isScrolling}>
            <ul>
                <li></li>
                <li>
                    {texts.navbar.feed}
                </li>
                <li>
                    <a href="#" on-click={this.addTrack}>
                        <i className="mf mf-plus-circled-2pt"/>
                    </a>
                </li>
            </ul>
        </nav>;
    },
    meNavbar(props) {
        let state = window.store.getState();

        return <nav id="navbar" classNames="me">
            <ul>
                <li>
                    <a href="#" on-click={this.addTrack}>
                        <i className="mf mf-bell"/>
                    </a>
                </li>
                <li></li>
                <li>
                    <a href="#" on-click={this.addTrack}>
                        <i className="mf mf-gear"/>
                    </a>
                </li>
            </ul>
        </nav>;
    },
    view(props) {
        return this[props.view+'Navbar'](props);
    }
};