/** @jsx html */
'use strict';

import {html}       from 'snabbdom-jsx';
import assign       from 'object-assign';
import humanFormat  from 'human-format';
import {fetchFeed}  from '../actions/creators/feed';
import {scrollFeed} from '../actions/creators/feed';
import Navbar       from './Navbar';

export default {
    onInsert() {
        setTimeout(() =>
            window.store.dispatch(fetchFeed()));
    },
    onScrollableInsert(vnode) {
        vnode.elm.addEventListener('scroll', this.onScroll.bind(this), true);
    },
    onScroll(evt) {
        if (!this.scrolling)
            this.scrollStart();

        clearTimeout(this.scrollTimer);
        this.scrollTimer = setTimeout(this.scrollStop.bind(this), 100);
    },
    scrollStart() {
        this.scrolling = true;
        window.store.dispatch(scrollFeed(true));
    },
    scrollStop() {
        this.scrolling = false;
        window.store.dispatch(scrollFeed(false));
    },
    onScrollableDestroy(vnode) {
        vnode.elm.removeEventListener('scroll', this.onScroll);
    },
    onWebkitTransitionEnd(evt) {
        //  fix for webkit issue: scrolling freezes
        //  after transition/animation
        evt.target.style.overflowY = 'hidden';
        setTimeout(() => evt.target.style.overflowY = 'auto');
    },
    view(props) {
        let state = window.store.getState();
        
        assign(props, {
            id: 'feed',
            classNames: 'view',
            class: {
                loading: state.feed.isFetching
            },
            'on-webkitTransitionEnd': this.onWebkitTransitionEnd,
            'hook-insert': this.onInsert
        });

        let scrollableProps = {
            'hook-insert': this.onScrollableInsert.bind(this),
            'hook-destroy': this.onScrollableDestroy.bind(this)
        };

        return <div {...props}>
            <Navbar view="feed"/>
            <div classNames="scrollable" {...scrollableProps}>
                <ul>
                    {state.feed.tracks.map(track =>
                    <li classNames="entry"
                        class-loved={track.is_liked}
                        style={{
                            backgroundImage: 'url('+track.picture+')'
                        }}>
                            <h3 classNames="title">
                                {track.name}
                            </h3>
                            <h6 classNames="author"
                                class-verified={track.is_verified_user}>
                                <u style={{
                                    backgroundImage: 'url('+track.author_picture+')'
                                }}/>
                                {track.author_name}
                            </h6>
                            <ul classNames="actions">
                                <li classNames="ellipsis">
                                    <i classNames="mf mf-ellipsis"/>
                                </li>
                                <li classNames="heart">
                                    <b>{humanFormat(track.likes_count, {
                                            decimals: 1
                                        })}
                                    </b>
                                    <i classNames="mf mf-heart"/>
                                </li>
                            </ul>
                    </li>)}
                </ul>
            </div>
        </div>;
    }
};