/** @jsx html */
'use strict';

import {html}           from 'snabbdom-jsx';
import Tabbar           from './Tabbar';
import ViewsContainer   from './ViewsContainer';

export default {
    onCreate() {
        if (!this.unsubscribe)
            this.unsubscribe = window.store.subscribe(window.patch);
    },
    view() {
        let props = {
                id: 'app',
                'hook-create': this.onCreate.bind(this)
            };

        return  <div {...props}>
                    <ViewsContainer/>
                    <Tabbar/>
                </div>;
    }
}
