'use strict';

import assign               from 'object-assign';
import {FETCH_FEED_REQ,
        FETCH_FEED_RES,
        FETCH_FEED_ERR,
        SCROLL_FEED_START,
        SCROLL_FEED_STOP}   from '../actions/types';

export default function feed(feed = {
    isFetching: false,
    tracks: [],
    fetchError: null,
    updatedAt: null,
    isScrolling: false
}, action) {
    switch (action.type) {
        case FETCH_FEED_REQ:
            return assign({}, feed, {
                isFetching: true,
                fetchError: null
            });
        case FETCH_FEED_RES:
            return assign({}, feed, {
                isFetching: false,
                tracks: action.tracks,
                updatedAt: action.updatedAt
            });
        case FETCH_FEED_ERR:
            return assign({}, feed, {
                isFetching: false,
                fetchError: action.error
            });
        case SCROLL_FEED_START:
        case SCROLL_FEED_STOP:
            return assign({}, feed, {
                isScrolling: action.type == SCROLL_FEED_START
            });
        default:
            return feed;
    }
}